  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.page_info h3 {
  margin-top: 25px;
  font-weight: 300;
  font-size: 35px;
  font-family: "Shippori Mincho", serif;
}
.page_info h5 {
  font-size: 18px;
  /* text-align: left; */
