  margin-top: 5px;
  font-weight: 400;
  margin-bottom: 40px;
}

.page_info h3 {
  font-family: "Teko", sans-serif;
  font-size: 55px;
  color: rgb(219, 6, 130);
  letter-spacing: 1px;
}

.page_info h5 {
  width: 650px;
}

.archives_body {
  background: rgb(255, 255, 255);
  display: flex;
}

.archives_body_card {
  margin-left: 15%;
}

@media screen and (max-width: 900px) {
  .archives_body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .archives_body_card {
    margin-left: 0px;
  }
}

.small_articles {
  margin-left: 10px;
}
@media screen and (max-width: 900px) {
  .page_info h3 {
    font-size: 40px;
    margin-top: 55px;
  }
  .small_articles {
    margin-left: 0px;
  }
}

@media screen and (max-width: 680px) {
  .page_info h5 {
    width: 90vw;
    font-size: 17px;
  }
}

@media screen and (max-width: 500px) {
  .page_info h5 {
    width: 95vw;
    font-size: 11px;
  }
}

.copyright h3 {
  text-align: center;
  color: white;
  font-weight: 300;
}

.copyright {
  border-top: 1px solid rgb(26, 25, 25);
  background: black;
  padding-top: 10px;
  padding-bottom: 10px;
}

.footer_logo h1 {
  text-align: center;
  /* background-color: aliceblue; */
  color: rgb(255, 255, 255);
  font-weight: 300;
  position: relative;
  top: 15px;
  font-size: 45px;
  font-family: "Oi", cursive;
  font-family: "Teko", sans-serif;
}

.footer_logo {
  height: 70px;
  display: flex;
  justify-content: center;
  border-top: 1px solid rgb(26, 25, 25);
  background: rgb(0, 0, 0);
}

