.faint1 {
  position: absolute;
  height: 100px;
  width: 100vw;
  background: linear-gradient(
    360deg,
    transparent,
    rgb(7, 7, 7),
    rgb(5, 5, 5),
    black,
    black,
    black
  );
  opacity: 0.98;
}

.faint2 {
  position: absolute;
  height: 50px;
  margin-top: -40px;
  width: 100vw;
  background: linear-gradient(
    180deg,
    transparent,
    rgb(7, 7, 7),
    rgb(5, 5, 5),
    black,
    black
  );
  opacity: 0.98;
}

