  display: flex;
  float: right;
}

.icon {
  /* transform: rotate(180deg); */
  transform: rotateZ(180deg);
  /* margin-right: 40px; */
  -webkit-animation: animate11 1s;
          animation: animate11 1s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes animate11 {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(180deg);
  }
}

@keyframes animate11 {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(180deg);
  }
}

.icon_rev {
  /* transform: rotate(180deg); */
  transform: rotateZ(0deg);
  /* margin-right: 40px; */
  -webkit-animation: animaterev 1s;
          animation: animaterev 1s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes animaterev {
  0% {
    transform: rotateZ(-180deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}

@keyframes animaterev {
  0% {
    transform: rotateZ(-180deg);
  }
  100% {
    transform: rotateZ(0deg);
